import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import styles from './styles.css'
import inputStyles from '_atoms/input/styles.css'

const LEVELS = [
  {
    value: 1,
    label: 'Novice',
    style: styles.levelNovice,
  },
  {
    value: 2,
    label: 'Beginner',
    style: styles.levelBeginner,
  },
  {
    value: 3,
    label: 'Skillful',
    style: styles.levelSkillful,
  },
  {
    value: 4,
    label: 'Experienced',
    style: styles.levelExperienced,
  },
  {
    value: 5,
    label: 'Expert',
    style: styles.levelExpert,
  },
]

const SkillLevelField = ({
  id,
  name,
  label,
  value,
  onChange
}) => {
  const moveNeedle = level => ({
    left: `${4 * 20}%`
  })

  return (
    <div className={inputStyles.container}>
      {label && <label className={inputStyles.label} htmlFor={id}>{label}</label>}
  
      <div className={classNames(inputStyles.input, styles.field)}>
        <div className={styles.needle} style={moveNeedle(value)} />
        {LEVELS.map((level, index) => {
          const isLevelActive = { [styles.activeLevel]: value === level.value };
  
          return (
            <button 
              key={index}
              type="button" 
              onClick={() => onChange(level.value)} 
              className={classNames(styles.level, level.style, isLevelActive)}
            />
          )
        })}
      </div>
    </div>
  )
}

SkillLevelField.propTypes = {
  id: PropTypes.string,
  name: PropTypes.string,
  label: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
}

SkillLevelField.propTypes = {
  id: '',
  name: '',
  label: '',
  value: '',
  onChange: () => {},
}

export default SkillLevelField
